  &-item {
    border: 1px solid rgba($gray, $alpha);
    border-radius: 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 515px;
    position: relative;
    padding: 2rem 1.9rem;
    box-sizing: border-box;
    box-shadow: 0 0 0 2px rgba($gray, 0);
    transition: 0.3s ease;
    overflow: hidden;
    flex: 0 0 24%;
    margin-bottom: 20px;

    @media (max-width: 1200px) {
      flex: 0 0 49%;
      text-align: center;
    }

    @media (max-width: 650px) {
      flex: 0 0 100%;
    }

    @media (max-width:360px) {
      min-height: inherit;
    }

    &:hover {
      box-shadow: 0 0 0 2px rgba($gray, $alpha);
    }

    &:hover {
      .slider-text {
        transform: translate(0, 0);
      }
    }

  }

  &-thumb {
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 204px;
    margin-bottom: 14px;

    @media (max-width:360px) {
      height: 130px;
    }

    &__image {
      position: absolute;
      max-height: 204px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      object-fit: cover;

      @media (max-width:360px) {
        height: 100%;
      }
    }

  }

}

.catalog-info {

  &__title {
    font-size: 1.875rem;
    font-family: "Cormorant", serif;
    margin-bottom: 17px;

    @media (max-width:1440px) {
      min-height: 69px;
    }

    @media (max-width:360px) {
      font-size: 1rem;
      margin-bottom: 9px;
      min-height: inherit;
    }

  }

  &__description {
    color: rgba($gray, 0.7);
    line-height: 1.5;
    margin-bottom: 0;
    min-height: 52px;
    max-width: 96%;

    @media(max-width: 1440px) {
      min-height: 80px;
    }

    @media (max-width:360px) {
      font-size: 0.71rem;
      min-height: inherit;
    }
  }

  &__price {
    font-size: 1.5rem;
    margin-top: 8px;

    @media (max-width:360px) {
      font-size: 0.85rem;
      margin-top: 4px;
    }
  }
}

.catalog-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 32px;
  margin-bottom: 8px;

  @media (max-width:1440px) {
    flex-direction: column;
    width: 100%;
  }

  @media (max-width:360px) {
    margin-top: 15px;
    margin-bottom: 0;
  }

  &__btn {
    padding: 1.3rem 2rem;
    cursor: pointer;
    font-size: 0.87rem;

    @media (max-width:1440px) {
      display: block;
      width: 100%;
    }

    @media (max-width:360px) {
      font-size: 0.71rem;
      padding: 1rem 1rem;
    }
  }
}

.catalog-count {
  display: flex;
  align-items: center;
  font-size: 1.5rem;

  @media (max-width:1440px) {
    width: 100%;
    margin-bottom: 15px;
  }

  @media (max-width: 1200px) {
    display: none;
  }

  &__minus,
  &__plus {
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    opacity: 0.4;
    transition: 0.2s ease;

    &:hover {
      opacity: 1;
    }
  }

  &__number {
    border: none;
    width: 30px;
    text-align: center;
  }

  &__minus {
    background-image: url(../img/minus.svg);
  }

  &__plus {
    background-image: url(../img/plus.svg);
  }

}

.loading-more {
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  opacity: 0.8;
  display: flex;
  justify-content: center;
  position: relative;
  padding-left: 2.43rem;
  letter-spacing: 0.06rem;
  transition: 0.2s ease;
  margin: 0 auto;
  cursor: pointer;

  @media (max-width: 360px) {
    font-size: 0.71rem;
  }

  &::before {
    content: "";
    display: block;
    width: 1.56rem;
    height: 1.56rem;
    left: 0;
    background-image: url(../img/refresh.png);
    background-size: cover;
    background-repeat: no-repeat;
    position: absolute;
    top: calc(50% - 0.78rem);
  }

  &:hover {
    opacity: 1;
  }

  &:focus {
    outline: none;

  }

}



.section-delivery {
  background-image: url(../img/bg-6.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding-bottom: 160px;

  @media (max-width:990px) {
    text-align: center;
  }

  @media (max-width:650px) {
    padding-bottom: 80px;
  }

  @media (max-width:360px) {
    padding-bottom: 53px;
  }

}

.box-delivery {
  display: flex;
  justify-content: space-between;
  margin-top: 56px;

  @media (max-width:990px) {
    flex-direction: column;
  }

  @media (max-width:360px) {
    margin-top: 36px;
  }

  &-item {
    flex: 0 0 50%;
    padding-right: 1%;

    &:last-child {
      flex: 0 0 41%;

      @media (max-width:1200px) {
        flex: 0 0 45%;
      }
    }

    .mr {
      @media (max-width:1200px) {
        margin-right: 0;
      }
    }
  }

  &__text {
    font-size: 1.12rem;
    line-height: 1.5;
    margin-bottom: 28px;

    @media (max-width:360px) {
      font-size: 0.85rem;
      margin-bottom: 21px;
    }
  }

  &__link {
    color: $gray;
    border-bottom: 1px solid $gray;
    text-decoration: none;
  }

  &__span {
    color: $orange;
  }
}

.box-delivery-list {

  list-style-type: none;
  display: flex;
  margin-top: 46px;

  @media (max-width:990px) {
    justify-content: center;
  }

  @media (max-width:360px) {
    margin-top: 37px;
  }

  &__item {
    display: block;
    background-repeat: no-repeat;
    height: 32px;
    margin-right: 1.93rem;
    opacity: 0.4;
    transition: 0.2s ease;

    &:hover {
      opacity: 1;
    }

    @media (max-width:990px) {
      margin-right: 20px;
      margin-left: 20px;
    }

    @media (max-width:650px) {
      height: 23px;
      margin-right: 10px;
      margin-left: 10px;
    }
  }

  &__mc {
    width: 53px;
    background-image: url(../img/mastercard.svg);
    background-size: 53px 32px;

    @media (max-width:650px) {
      background-size: 38px 23px;
      background-position: center center;
    }
  }

  &__visa {
    width: 74px;
    background-image: url(../img/visa.svg);
    background-size: 74px 32px;
    background-position: 0 -2px;

    @media (max-width:650px) {
      background-size: 53px 17px;
      background-position: center 3px;
    }
  }

  &__paypal {
    width: 87px;
    background-image: url(../img/paypal.svg);
    background-size: 87px 24px;
    background-position: 0 4px;

    @media (max-width:650px) {
      background-size: 63px 17px;
      background-position: center 4px;
    }
  }


}

.section-form {
  background-image: url(../img/bg-7.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  @media (max-width:990px) {
    text-align: center;
  }
}

.box-form {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 160px;

  @media (max-width:990px) {
    flex-direction: column;
  }

  @media (max-width:650px) {
    padding-bottom: 80px;
  }

  @media (max-width:360px) {
    padding-bottom: 61px;
  }

  .title {
    margin-bottom: 60px;

    @media (max-width:360px) {
      margin-bottom: 31px;
    }
  }

  &__text {
    font-size: 1.12rem;
    line-height: 1.5;

    @media (max-width:990px) {
      max-width: 658px;
    }

    @media (max-width:650px) {
      max-width: 400px;
    }

    @media (max-width:360px) {
      font-size: 0.85rem;
    }
  }

  &-item {
    flex: 0 0 39%;

    &-item:last-child {
      flex: 0 0 41%;

      @media (max-width:990px) {
        margin-top: 3rem;
      }

      @media (max-width:360px) {
        margin-top: 2.6rem;
      }
    }
  }
}

.box-form-list {
  list-style-type: none;
  margin-top: 62px;

  @media (max-width:990px) {
    display: flex;
    justify-content: center;
  }

  @media (max-width:650px) {
    margin-top: 2rem;
  }

  @media (max-width:360px) {
    margin-top: 1.4rem;
  }

  &__item {
    position: relative;
    padding-left: 58px;
    margin-bottom: 20px;

    @media (max-width:990px) {
      padding-left: 44px;
      margin-left: 20px;
      margin-right: 20px;
    }

    @media (max-width:650px) {
      padding-left: 28px;
      margin-left: 10px;
      margin-right: 10px;
    }

    &:last-child {
      margin-bottom: 0;

      @media (max-width:990px) {
        margin-bottom: 20px;
      }
    }

    &::before {
      content: '';
      display: block;
      width: 29px;
      height: 25px;
      background-repeat: no-repeat;
      position: absolute;
      left: 0;
      top: calc(50%-12.5px);

      @media (max-width:650px) {
        width: 18px;
        height: 16px;
        top: calc(50% - 8px);
      }
    }
  }

  &__tel {
    &::before {
      background-image: url(../img/phone.svg);
    }

  }

  &__email {
    &::before {
      background-image: url(../img/mail.svg);
      background-position: 0 center;
    }

  }

  &__link {
    color: $gray;
    text-decoration: none;
    font-style: 1.5rem;
    font-weight: 500;

    @media (max-width:650px) {
      font-size: 0.85rem;
    }
  }
}

.form {
  border: 2px solid rgba($gray, $alpha);
  border-radius: 20px;
  padding: 2rem 3.5rem 1.8rem 3.5rem;

  @media (max-width: 650px) {
    padding: 2rem 1.5rem 1.8rem;
  }

  @media (max-width:360px) {
    padding: 1.4rem 1.2rem 1rem;
  }

  &__h3 {
    font-family: "Cormorant", serif;
    font-size: 1.87rem;
    margin-bottom: 31px;

    @media (max-width:360px) {
      font-size: 1.57rem;
    }
  }

  &__label {
    display: block;
    font-size: 0.75rem;
    margin-bottom: 10px;
    margin-left: 5px;

    @media (max-width:990px) {
      text-align: left;
    }

    @media (max-width:360px) {
      margin-bottom: 3px;
    }

  }

  &__input {
    display: block;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid rgba($gray, $alpha);
    border-radius: 20px;
    padding: 1.4rem 1.9rem;
    margin-bottom: 29px;
    transition: 0.2s ease;

    @media (max-width:360px) {
      font-size: 0.85rem;
      padding: 1.1rem 1.5rem;
      margin-bottom: 9px;
    }

    &:focus {
      outline: none;
      border: 1px solid $gray;
    }
  }

  &__text {
    font-size: 0.75rem;
    opacity: 0.6;
    margin-top: 15px;
    line-height: 1.5;

    @media (max-width:990px) {
      margin-top: 1.5rem;
    }

    @media (max-width:360px) {
      margin-top: 1.3rem;
    }
  }

  &__link {
    color: $gray;
    border-bottom: 1px solid rgba($gray, 0.6);
    text-decoration: none;
  }

  .btn {
    margin-top: 11px;
    padding: 1.5rem 3.1rem;

    @media (max-width:360px) {
      font-size: 0.85rem;
      padding: 1rem 2rem;
      margin-top: 20px;
    }

    &:focus {
      outline: none;
    }
  }
}

.modal-header {
  border-bottom: none;
  height: 50px;
  padding: 0;

  button.close {
    border: 2px solid $gray;
    border-radius: 100%;
    padding: 0;
    width: 30px;
    height: 30px;
    line-height: 0;
    font-weight: 300;
    position: absolute;
    top: 37px;
    right: 34px;
    padding-bottom: 4px;

    &:focus {
      outline: none;
    }

  }
}

.modal-body {
  padding: 1rem 1rem 3.2rem 1rem;
}

.modal-social {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  max-width: 210px;
  margin: 0 auto;

  &__link {
    width: 48px;
    height: 48px;
    border: 2px solid $gray;
    border-radius: 100%;
    background-repeat: no-repeat;
    display: block;
    background-position: center center;
    opacity: 0.8;
    transition: 0.2s ease;

    &:hover {
      opacity: 1;
    }
  }

  &__vk {
    background-image: url(../img/vk.svg);
    background-size: 23px 18px;
  }

  &__inst {
    background-image: url(../img/instagram.svg);
    background-size: 18px 18px;
  }

  &__fb {
    background-image: url(../img/facebook.svg);
    background-size: 18px 18px;
  }
}

.modal-list {
  list-style-type: none;
  text-align: center;
  margin-bottom: 39px;

  &__item {
    margin-bottom: 1.37rem;
  }

  &__link {
    color: $gray;
  }
}