.header {
    max-width: 1440px;
    margin: 0 auto;
    padding-top: 40px;
    padding-bottom: 46px;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media(max-width: 1440px) {
        max-width: 1140px;
    }

    @media(max-width: 1200px) {
        max-width: 960px;
        padding-bottom: 65px;
    }

    @media(max-width: 990px) {
        max-width: 100%;
    }

    @media (max-width:360px) {
        padding-top: 15px;
        padding-bottom: 37px;
    }

    &-item {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__tel {
        text-decoration: none;
        color: $gray;
        transition: 0.3s ease;
        font-weight: 500;
        letter-spacing: 0.075rem;

        @media(max-width: 990px) {
            text-indent: -9999px;
            width: 2.5rem;
            height: 2.5rem;
            background-color: transparent;
            border: 1px solid $gray;
            background-image: url(../img/tel.svg);
            border-radius: 100%;
            background-size: 1rem 0.87rem;
            background-repeat: no-repeat;
            background-position: center center;
        }

        &:hover {
            color: $orange;
        }

    }

    &__menu {

        @media(min-width: 991px) {
            display: none;
        }

        @media(max-width: 990px) {
            display: block;
            width: 2.5rem;
            height: 2.5rem;
            background-color: transparent;
            border: 1px solid $gray;
            background-image: url(../img/menu.svg);
            border-radius: 100%;
            background-size: 1rem 0.87rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin-left: 20px;
        }

        @media (max-width:360px) {
            margin-left: 12px;
        }

        &:focus {
            outline: none;
        }
    }


}


.header-cart {
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    margin-left: 80px;

    @media(max-width: 1440px) {
        margin-left: 40px;
    }

    @media(max-width: 1200px) {
        margin-left: 20px;
    }

    @media (max-width:360px) {
        margin-left: 12px;
    }

    &__button {
        width: 2.5rem;
        height: 2.5rem;
        background-color: $orange;
        border: none;
        border-radius: 100%;
        background-image: url(data:img/png;base64,);
        background-size: 1rem 0.87rem;
        background-repeat: no-repeat;
        background-position: center center;

        @media(max-width: 990px) {
            background-color: transparent;
            border: 1px solid $gray;
            background-image: url(../img/cart-black.svg);
        }


        &:focus {
            outline: none;
        }
    }

    &__count {
        position: absolute;
        top: 6px;
        right: 9px;
        font-size: 0.5rem;
        color: $white;

        @media(max-width: 990px) {
            color: $gray;
        }
    }
}

.logo {
    display: block;
    background-repeat: no-repeat;
    background-image: url(../img/logo.svg);
    width: 120px;
    height: 32px;
    margin-right: 120px;
    background-size: contain;

    @media(max-width: 1440px) {
        margin-right: 50px;
    }

    @media(max-width: 1200px) {
        margin-right: 32px;
    }

    @media (max-width:360px) {
        width: 97px;
        height: 26px;
        margin-right: 15px;
    }
}

nav {

    @media(max-width: 990px) {
        display: none;
    }

}

.nav {
    list-style-type: none;
    display: flex;
    justify-content: space-between;

    &-item {
        margin-right: 48px;

        @media(max-width: 1440px) {
            margin-right: 30px;
        }

        @media(max-width: 1200px) {
            margin-right: 18px;
        }
    }

    &-link {
        padding: 0;
        text-decoration: none;
        color: $gray;
        transition: 0.3s ease;

        &:hover {
            color: $orange;
        }
    }
}